import React from "react";
import {connect, ConnectedProps} from "react-redux";
import {changeWojewodztwo, pobierzListePowiatow} from "./konfiguracja/actions";
import {RootState} from "./reducers";

interface SelektorWojewodztwaProps extends PropsFromRedux{
}

const WOJEWODZTWA = [
    "DOLNOŚLĄSKIE",
    "KUJAWSKO-POMORSKIE",
    "LUBELSKIE",
    "LUBUSKIE",
    "ŁÓDZKIE",
    "MAŁOPOLSKIE",
    "MAZOWIECKIE",
    "OPOLSKIE",
    "PODKARPACKIE",
    "PODLASKIE",
    "POMORSKIE",
    "ŚLĄSKIE",
    "ŚWIĘTOKRZYSKIE",
    "WARMIŃSKO-MAZURSKIE",
    "WIELKOPOLSKIE",
    "ZACHODNIOPOMORSKIE",
]

class SelektorWojewodztwaInternal extends React.Component<SelektorWojewodztwaProps> {
    render() {
        const {pobierzListePowiatow, wojewodztwo, changeWojewodztwo} = this.props;
        return <select
            value={wojewodztwo}
            id="selektor-wojewodztwa"
            onChange={ev => {
                changeWojewodztwo(ev.target.value);
                pobierzListePowiatow(ev.target.value)}}>
            {WOJEWODZTWA.map((name) => (
                <option key={name} value={name}>
                    {name}
                </option>
            ))}
        </select>;
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        wojewodztwo: state.wojewodztwo,
    }
}

const mapDispatchToProps = {
    pobierzListePowiatow,
    changeWojewodztwo,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(SelektorWojewodztwaInternal);
